import { classMap } from 'lit/directives/class-map.js';
import { html } from 'lit';
import { property } from 'lit/decorators.js';
import componentStyles from '../../styles/component.styles';
import styles from './badge.styles';
import UIKitElement from '../../internal/uikit-element';
import type { CSSResultGroup } from 'lit';

/**
 * @summary Badges are used to draw attention and display statuses or counts.
 * @documentation https://kind-desert-0fb80550f.5.azurestaticapps.net/?path=/docs/components-badge--docs
 * @status stable
 * @since 2.0
 *
 * @slot - The badge's content.
 *
 * @csspart base - The component's base wrapper.
 * @csspart label - The chip's label.
 */
export default class Badge extends UIKitElement {
  static styles: CSSResultGroup = [componentStyles, styles];

  /** The badge's theme variant. */
  @property({ reflect: true }) size: 'extra-small' | 'small' | 'medium' | 'large' = 'medium';

  render() {
    return html`
      <span
        part="base"
        class=${classMap({
          badge: true,
          'badge--extra-small': this.size === 'extra-small',
          'badge--small': this.size === 'small',
          'badge--medium': this.size === 'medium',
          'badge--large': this.size === 'large'
        })}
        role="status"
      >
        ${this.size === 'large' ? html`<slot part="label"></slot>` : ''}
      </span>
    `;
  }
}
